	.nav-controls {
		position: fixed;
		top: 0;
		z-index: 100000;
		height: 118px;
		width: 100%;
	}

	.widgets {
		margin-top: 118px;
	}


/* ------- Control 1) Checkboxes ------- */
	.pi-checkboxes-all {
		display: 			flex;
		flex-direction: 	row;
		flex-wrap: 			wrap;
		justify-content: 	space-around;
		align-items: 		stretch;
		
		background-color: #333;
		line-height: 		100%;
		padding: 			5px 0;
		margin-bottom: 	0px;
	}

	/* The checkbox or radio + label pair, in flex mode */
	.pi-checkboxes-all div {
		flex: 				0 0 25%;
		align-items: 		stretch;
		margin: 				3px 5px;
		border: 				1px solid rgba(255,255,255,0.08);
		border-radius: 	2px;
		background-color: rgba(255,255,255,0.05);
		padding: 			3px 6px;
	}

	.pi-checkboxes-all input {
		padding: 			0;
		margin: 				0;
		line-height: 		normal;
		position: 			static;
		opacity: 			1;
		z-index: 			1;
		width: 				14px;
		height: 				14px;
	}

	/* The label for both checkbox and radio */
	.pi-checkbox {
		color: 				white;
		font-family: 		'helvetica';
		font-size: 			12px; 
		padding: 			0 0 0 0;
		margin: 				0 0 0 3px;
		line-height: 		normal;
	}




/* ------- Control 3) Rulers ------- */
	#pi-rulers {
		margin-bottom: 	0px;
	}

	#pi-rulers div {
		display: 		 	flex;
		justify-content: 	center;
		flex-wrap: 		 	wrap;		
		
		font-size: 			12px;
		font-family: 		'helvetica';
		font-weight: 		normal;
		font-style: 		normal;
		text-align: 		center;
		color: 				white;
		line-height: 		100%;
		border-bottom: 	0px dashed black;
		border-top: 		0px dashed black;
		margin-bottom: 	0;
		padding: 			3px 0 !important;
		border-bottom-left-radius: 100px;
		border-bottom-right-radius: 100px;
	}

	#pi-rulers div span {
		flex: 				0 1 auto;
		margin: 				0 10px;
		border: 				1px solid rgba(255,255,255,0.2);
		border-radius: 	3px;
		background-color: rgba(255,255,255,0.1);
		padding: 			3px 12px;
	}

	#pi-rulers div:nth-child(1) {
		background-color: $desktop-color;
	}
	#pi-rulers div:nth-child(2) {
		background-color: $tablet-color;
	}
	#pi-rulers div:nth-child(3) {
		background-color: $phone414-color;
	}
	#pi-rulers div:nth-child(4) {
		background-color: $phone375-color;
	}
	#pi-rulers div:nth-child(5) {
		background-color: $phone320-color;
	}




