/* ------- Survey Widget: Container and Label ------- */
.widgetPad {
	font-family: 		'helvetica';
	font-size: 			11px;
	font-weight: 		normal;
	text-align: 		center;
	text-transform: 	uppercase;
	margin-bottom: 	30px;
	border-bottom: 	0px dashed #ccc;
	border-top: 		0px dotted blue;
	border-radius: 	0px 0 100px 0;
}




/* ------- Survey Widget: Widths and Tab Colors ------- */
	/* .desktop 	{background-color: $desktop-color; 	min-width:769px !important} */
	.desktop 	{ min-width:769px !important }
	.tablet 		{ max-width:768px }
	.phone414 	{ max-width:414px }
	.phone375 	{ max-width:375px }
	.phone320 	{ max-width:320px }




/* -------------- Media Queries to Hide Device Types -------------- */
	@media (max-width:768px) {	.desktop		{ display: none !important; } }
	@media (min-width:769px) {	.tablet		{ display: none !important; } }
	@media (max-width:414px) {	.tablet		{ display: none !important; } }
	@media (min-width:415px) {	.phone414	{ display: none !important; } }
	@media (max-width:375px) {	.phone414	{ display: none !important; } }
	@media (min-width:376px) {	.phone375	{ display: none !important; } }
	@media (max-width:320px) {	.phone375	{ display: none !important; } }
	@media (min-width:321px) {	.phone320	{ display: none !important; } }

